.keyboard-line{
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-content: center;
}

.sq{
	width: 50px;
	height: 50px;
	text-align: center;
	border: 1px solid black;
	margin: 5px;
	cursor: pointer;
	border-radius: 5px;
	text-transform: capitalize;
}

.sq:hover{
	transform: scale(1.05);
	background-color: beige;
	font-weight: 600;
}
